body {
  margin: 0;
  line-height: normal;
}

:root {
  /* fonts */
  --font-playfair-display: "Playfair Display";
  --font-poller-one: "Poller One";

  /* font sizes */
  --font-size-base: 16px;
  --font-size-49xl-1: 68.1px;

  /* Colors */
  --color-white: #fff;
  --color-lightgray: #ced4da;
  --color-slategray: rgba(108, 117, 125, 0.65);
  --color-gray-100: #141414;
  --color-gray-200: rgba(255, 254, 254, 0.5);
  --color-gainsboro: #d9d9d9;
  --color-black: #000;
}
